#bio {
  text-align: left;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 500;
  font-size: 20px;
  color: rgb(0, 0, 0);
  margin-left: 50px;
  line-height: 3;
}

.personal-project-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

.card {
  margin: 10px;
  justify-content: space-between;
}

.card-body {
  max-height: 150px;
}

.card-img-top {
  max-height: 300px;
}
